<template>
  <div>
    <!-- <button @click='click'>btn</button> -->
    <router-view/>
  </div>
</template>

<script>
import {MessageBox} from 'element-ui'

export default {
  name: 'AppMain',
  methods: {
    click() {
      MessageBox.confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).then(() => {
        this.$store.dispatch('LogOut').then(() => {
          location.href = '/index';
        })
      }).catch(() => {});
    }
  }
}
</script>
