import { getToken, removeToken, setToken } from '@/utils/auth'
import { login, logout, getInfo } from '@/api/login'


const user = {
  state: {
    token: getToken(),
    name: '',
    avatar: '', //getInfo
    roles: [],
    permissions: [],
  },
  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
    },
    SET_NAME: (state, name) => {
      state.name = name
    },
    SET_AVATAR: (state, avatar) => {
      state.avatar = avatar
    },
    SET_ROLES: (state, roles) => {
      state.roles = roles
    },
    SET_PERMISSIONS: (state, permissions) => {
      state.permissions = permissions
    }
  },
  actions: {
    //payload
    Login(context, loginForm){
      const username = loginForm.username.trim()
      const passwd = loginForm.passwd
      const code = loginForm.code
      const uuid = loginForm.uuid
      //Promise.then.catch
      return new Promise((resolve, reject) => {
        login(username, passwd, code, uuid).then(res => { //loginthen
          setToken(res.token)
          context.commit('SET_TOKEN', res.token)
          resolve() //then
        }).catch(err => { //catch
          reject(err) //catch
        })
      })
    },

    // 
    GetInfo({ commit, state }) {
      return new Promise((resolve, reject) => {
        getInfo(state.token).then(res => { //avatar""
          const user = res.user
          const avatar = user.avatar == "" ? require("@/assets/images/profile.jpg") : process.env.VUE_APP_BASE_API + user.avatar;
          if (res.roles && res.roles.length > 0) { // roles
            commit('SET_ROLES', res.roles)
            commit('SET_PERMISSIONS', res.permissions)
          } else {
            commit('SET_ROLES', ['ROLE_DEFAULT'])
          }
          commit('SET_NAME', user.userName)
          commit('SET_AVATAR', avatar)
          resolve(res)
        }).catch(error => { //getInfo.catch.thentypeError
          console.log('getInfo failed')
          reject(error)
        })
      })
    },

    LogOut({commit, state}){
      //Promise
      return new Promise((resolve, reject) => {
        logout(state.token).then(() => { //logoutthen
          commit('SET_TOKEN', '')
          commit('SET_ROLES', [])
          commit('SET_PERMISSIONS', [])
          removeToken()
          resolve() //resolvethen
        }).catch(err => { //logoutcatch
          reject(err) //catch
        })
      })
    },

    FedLogOut({commit}){ //
      return new Promise((resolve, reject) => {
        console.log('in fedlogout')
        commit('SET_TOKEN', '')
        removeToken()
        resolve()
      })
    }
  }
}


export default user
