import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '@/layout'

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    component: () => import('@/views/login')
  },
  {
    path: '/401',
    component: () => import('@/views/error/401')
  },
  {
    path: '/404',
    component: () => import('@/views/error/404')
  },
  {
    path: '/',
    component: Layout,
    redirect: '/index',
    children: [
      {
        path: 'index',
        component: () => import('@/views/index'),
        name: '', //name:key=item.path
        meta: { title: '', icon: 'dashboard', noCache: true, affix: true },
      },
      {
        path: 'wiki',
        component: () => import('@/views/wiki'),
        meta: { title: 'Wiki', icon: 'dashboard', noCache: true, affix: true },
      },
      {
        path: 'file-list',
        component: () => import('@/views/file-list'),
        meta: { title: '', icon: 'dashboard', noCache: true, affix: true },
      },
      {
        path: 'projects',
        component: () => import('@/views/projects'),
        meta: { title: '', icon: 'dashboard', noCache: true, affix: true },
      },
      {
        path: 'user-space/',
        component: () => import('@/views/user-space'),
        redirect: 'user-space/profile',
        children: [
          {
            path: 'profile',
            component: () => import('@/views/user-space/profile'),
            meta: { title: '', icon: 'dashboard', noCache: true, affix: true },
          },
          {
            path: 'learning-space',
            component: () => import('@/views/user-space/learning-space'),
            meta: { title: '', icon: 'dashboard', noCache: true, affix: true },
          }
        ]
      },
    ]
  },

]


const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
