<template>
  <!-- showLogotruesidebarlogo -->
  <div :class="{'has-logo':showLogo}" :style="{ backgroundColor: settings.sideTheme === 'theme-dark' ? variables.menuBg : variables.menuLightBg }">
    <!-- logo -->
    <logo v-if="showLogo" :collapse="isCollapse" />
    <el-scrollbar :class="settings.sideTheme" wrap-class="scrollbar-wrapper">
      <el-menu
        :default-active="activeMenu"
        :collapse="isCollapse"
        :background-color="settings.sideTheme === 'theme-dark' ? variables.menuBg : variables.menuLightBg"
        :text-color="settings.sideTheme === 'theme-dark' ? variables.menuText : 'rgba(0,0,0,.65)'"
        :unique-opened="true"
        :active-text-color="settings.theme"
        :collapse-transition="false"
        mode="vertical"
      >
        <!-- app-linkrouter-link<a>el-menu-item -->
        <app-link to="/">
          <el-menu-item index="1" :class="{'submenu-title-noDropdown':true}">
            <!-- itemicontitle -->
            <item icon="tree" title="" />
          </el-menu-item>
        </app-link>

        <app-link to="/wiki">
          <el-menu-item index="2" :class="{'submenu-title-noDropdown':true}">
            <item icon="education" title="Wiki" />
          </el-menu-item>
        </app-link>

        <app-link to="/file-list">
          <el-menu-item index="3" :class="{'submenu-title-noDropdown':true}">
            <item icon="documentation" title="" />
          </el-menu-item>
        </app-link>

        <app-link to="/projects">
          <el-menu-item index="3" :class="{'submenu-title-noDropdown':true}">
            <item icon="dashboard" title="" />
          </el-menu-item>
        </app-link>

        <el-submenu ref="subMenu" index="4" popper-append-to-body>
          <template slot="title">
            <item icon="user" title="" />
          </template>

          <app-link to="/user-space/profile">
            <el-menu-item index="4-1" :class="{'submenu-title-noDropdown':false}">
              <item icon="system" title="" />
            </el-menu-item>
          </app-link>

          <app-link to="/user-space/learning-space">
            <el-menu-item index="4-2" :class="{'submenu-title-noDropdown':false}">
              <item icon="server" title="" />
            </el-menu-item>
          </app-link>
        </el-submenu>

      </el-menu>
    </el-scrollbar>
  </div>
</template>

<script>
import {mapGetters, mapState} from 'vuex'
import variables from '@/assets/styles/variables.scss'
import Item from './Item'
import AppLink from './Link'
import Logo from './Logo'

export default {
  name: 'SideBar',
  components: {
    Item,
    AppLink,
    Logo,
  },
  computed: {
    ...mapState(['settings']), //$store.state.settingsthis
    ...mapGetters(['sidebar']), //getterssidebarthisthis.$store.commit('sidebar')this.sidebar
    activeMenu() { //menumeta
      const route = this.$route;
      const { meta, path } = route;
      // if set path, the sidebar will highlight the path you set
      if (meta.activeMenu) {
        return meta.activeMenu;
      }
      return path; //path
    },
    showLogo() { //sidebar logo
      console.log(this.$store.state.settings.sidebarLogo)
      return this.$store.state.settings.sidebarLogo;
    },
    variables() {
      return variables;
    },
    isCollapse() { //sidebar
      return !this.sidebar.opened;
    }
  }
}
</script>
